.navigation {
  background-color: #81b3d2;
  display: flex;
  flex-direction: column;
  align-items: center;
  display: none;
}
.navigation__item {
  box-shadow: inset 0 -1px 0 0 #81b3d2, inset 0 -3px 0 0 rgba(0, 0, 0, 0.2), inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
  width: 100%;
  text-align: center;
}
.navigation__link {
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  width: 100%;
}
.header {
  display: flex;
  flex-direction: column-reverse;
}
.header__logo {
  display: block;
  min-height: 57px;
  position: absolute;
  left: 50%;
  margin-left: -52px;
}
.navigation__btn-close {
  border: none;
  background-color: transparent;
  position: relative;
  top: -20px;
  left: 96px;
}
.navigation__btn-close::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 4px;
  background-color: #fff;
  top: 11px;
  right: -1px;
  transform: rotate(45deg);
}
.navigation__btn-close::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 4px;
  background-color: #fff;
  top: 11px;
  right: -1px;
  transform: rotate(-45deg);
}
.header__menu-btn {
  border: none;
  background-color: #ffffff;
  width: 29px;
  height: 29px;
  position: relative;
  margin-right: 20px;
  margin-top: 15px;
}
.header__menu-btn::after {
  content: "";
  position: absolute;
  width: 23px;
  height: 3px;
  background-color: #81b3d2;
  top: 2px;
  right: 0;
  box-shadow: 0 17px 0 0 #81b3d2, 0 8.5px 0 0 #81b3d2;
}
.header__logo__inner {
  min-height: 58px;
  position: relative;
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .header__logo__inner {
    min-height: auto;
  }
  .header__menu-btn {
    display: none;
  }
  .header__menu-btn ::before,
  ::after {
    display: none;
  }
  .navigation__link {
    width: auto;
    color: #000;
    border: none;
    font-size: 14px;
  }
  .navigation__item {
    box-shadow: none;
    width: 147px;
  }
  .navigation--wrapper {
    display: flex;
    flex-direction: row;
  }
  .navigation {
    min-height: 57px;
    flex-direction: row;
    justify-content: space-between;
    background-color: #ffffff;
    position: relative;
    display: flex;
  }
  .header__logo {
    position: absolute;
    left: 50%;
    top: -57px;
    z-index: 1;
    margin-left: -45px;
  }
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
body {
  font-family: "PT Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.common--title {
